@import '@/assets/newCss/flex.scss';
@import '@/assets/newCss/font.scss';
@import '@/assets/newCss/mixins.scss';

.qsList {
  width: 100%;
  @include fcss;
  gap: var(--spacing-16);
  .title {
    @include HeadlineH2;
  }
  .qss {
    @include fcss;
    width: 100%;
    gap: var(--spacing-24);
    .qs {
      @include fcss;
      width: 100%;
      gap: var(--spacing-8);
      padding-bottom: var(--spacing-24);
      border-bottom: 1px solid var(--border-tokens-border-primary);
      &:last-child {
        border-bottom: 0;
      }
      .question {
        @include frsc;
        @include HeadlineH3;
        gap: var(--spacing-8);
        .iconfont {
          font-size: var(--spacing-24);
        }
      }
      .answer {
        @include BodyBody200;
        color: var(--text-tokens-text-secondary);
        .subReasonItems {
          @include fcss;
          > li {
            @include frsc;
            gap: 6px;
            .dot {
              margin: 0 6px;
              width: 2.5px;
              height: 2.5px;
              border-radius: 50%;
              background: var(--text-tokens-text-secondary);
            }
          }
        }
      }
    }
  }
}
