@import '@/assets/newCss/flex.scss';
@import '@/assets/newCss/font.scss';
@import '@/assets/newCss/mixins.scss';

.assetsDetails {
  width: 100%;
  @include fcss;
  gap: var(--spacing-16);
  flex: 1;
  .title {
    width: 100%;
    @include HeadlineH3;
    @include frbc;
    .operations {
      @include frsc;
      gap: var(--spacing-16);
      button.PButton.text{
        height: fit-content;
      }
    }
  }
  .content {
    width: 100%;
    padding: var(--spacing-16) var(--spacing-24);
    border-radius: var(--spacing-8);
    border: 1px solid var(--border-tokens-border-primary);
    background: var(--layer-tokens-layer-primary);
    @include fcss;
    gap: var(--spacing-24);

    .PTabs {
      width: 100%;
      .tab{
        .con{
          @include frsc;
        }
      }
    }
  }
}
