.TextLinkLink {
  font-feature-settings: "ss08" on, "clig" off, "liga" off;
  /* TextLink/Hover */
  font-family: "IBM Plex Sans";
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  letter-spacing: 0px;
  line-height: 24px;
}
.TextLinkLink:hover {
  text-decoration-line: underline;
}

.textlink {
  font-size: 14px;
  font-family: IBM Plex Sans;
  font-weight: 400;
  letter-spacing: 0px;
  line-height: 18px;
}

.button-button {
  font-size: 14px;
  font-family: IBM Plex Sans;
  font-weight: 500;
  letter-spacing: 0.1599999964px;
  line-height: 18px;
}

.number-time {
  font-size: 14px;
  font-family: IBM Plex Mono;
  font-weight: 400;
  letter-spacing: 0px;
  line-height: 18px;
}

.number-h1 {
  font-size: 20px;
  font-family: IBM Plex Mono;
  font-weight: 500;
  letter-spacing: 0px;
  line-height: 28px;
}

.number-body {
  font-size: 12px;
  font-family: IBM Plex Mono;
  font-weight: 400;
  letter-spacing: 0px;
  line-height: 125%;
}

.light,
.white,
#pado-extension-content,
.light #pado-extension-content,
.white #pado-extension-content {
  --shadow-color: rgba(0, 0, 0, 0.06);
  --button-tokens-button-primary: #161616;
}

.dark,
.black,
.dark #pado-extension-content,
.black #pado-extension-content {
  --shadow-color: rgba(0, 0, 0, 0.12);
  --button-tokens-button-primary: #fffff;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.padoDialogHeaderStyle, .pDialog2 > main > header {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: var(--spacing-24);
}
.padoDialogHeaderStyle h1, .pDialog2 > main > header h1 {
  font-feature-settings: "ss08" on, "clig" off, "liga" off;
  font-family: "IBM Plex Sans";
  font-size: 24px;
  font-style: normal;
  font-weight: 600;
  line-height: 36px;
  color: var(--text-tokens-text-primary);
}
.padoDialogHeaderStyle h2, .pDialog2 > main > header h2 {
  font-feature-settings: "ss08" on, "clig" off, "liga" off;
  font-family: "IBM Plex Sans";
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.16px;
  color: var(--text-tokens-text-secondary);
}

.pDialog2 {
  display: flex;
  flex-direction: column;
  justify-content: space-between;
  align-items: center;
  width: var(--padoDialog-width, 448px);
  border-radius: var(--spacing-8, 8px);
  background: var(--layer-tokens-layer-primary, #fff);
  padding: var(--spacing-24, 24px);
  color: var(--text-tokens-text-primary);
  position: relative;
}
.pDialog2 .PClose {
  position: absolute;
  top: var(--spacing-24, 24px);
  right: var(--spacing-24, 24px);
  top: 24px;
  right: 24px;
}
.pDialog2 > main {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  gap: var(--spacing-24);
}
.pDialog2 > main .pFormWrapper {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  width: 100%;
  gap: var(--spacing-24);
}
.pDialog2 > main .pFormWrapper .formItem {
  width: 100%;
}
.pDialog2 > main .pFormWrapper > .PButton {
  width: 100%;
  height: var(--spacing-40, 40px);
}
.pDialog2 > footer {
  width: 100%;
}
.pDialog2 > footer > .PButton {
  width: 100%;
  height: var(--spacing-40, 40px);
}