.TextLinkLink {
  font-feature-settings: "ss08" on, "clig" off, "liga" off;
  /* TextLink/Hover */
  font-family: "IBM Plex Sans";
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  letter-spacing: 0px;
  line-height: 24px;
}
.TextLinkLink:hover {
  text-decoration-line: underline;
}

.textlink {
  font-size: 14px;
  font-family: IBM Plex Sans;
  font-weight: 400;
  letter-spacing: 0px;
  line-height: 18px;
}

.button-button {
  font-size: 14px;
  font-family: IBM Plex Sans;
  font-weight: 500;
  letter-spacing: 0.1599999964px;
  line-height: 18px;
}

.number-time {
  font-size: 14px;
  font-family: IBM Plex Mono;
  font-weight: 400;
  letter-spacing: 0px;
  line-height: 18px;
}

.number-h1 {
  font-size: 20px;
  font-family: IBM Plex Mono;
  font-weight: 500;
  letter-spacing: 0px;
  line-height: 28px;
}

.number-body {
  font-size: 12px;
  font-family: IBM Plex Mono;
  font-weight: 400;
  letter-spacing: 0px;
  line-height: 125%;
}

.light,
.white,
#pado-extension-content,
.light #pado-extension-content,
.white #pado-extension-content {
  --shadow-color: rgba(0, 0, 0, 0.06);
  --button-tokens-button-primary: #161616;
}

.dark,
.black,
.dark #pado-extension-content,
.black #pado-extension-content {
  --shadow-color: rgba(0, 0, 0, 0.12);
  --button-tokens-button-primary: #fffff;
}

@keyframes spin {
  0% {
    transform: rotate(0deg);
  }
  100% {
    transform: rotate(360deg);
  }
}
.tableSection {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: var(--spacing-16);
}
.tableSection .dataSourceItems {
  width: 100%;
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: var(--spacing-8);
  margin-bottom: var(--spacing-8);
}
.tableSection .dataSourceItems.noMb {
  margin-bottom: 0;
}
.tableSection .dataSourceItems .dataSourceItem {
  width: 100%;
  padding: var(--spacing-16);
  border-radius: var(--spacing-8);
  background: var(--layer-tokens-layer-primary);
  background-color: var(--layer-tokens-layer-on-primary);
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: var(--spacing-24);
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo {
  width: 100%;
  height: 44px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .left {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: var(--spacing-8);
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .left .pStar .iconfont {
  font-size: var(--spacing-16);
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .left .sourceIcon {
  width: var(--spacing-40);
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .left .breif {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: 2px;
  font-feature-settings: "ss08" on, "clig" off, "liga" off;
  font-family: "IBM Plex Sans";
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  letter-spacing: 0px;
  line-height: 18px;
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .left .breif .top {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: var(--spacing-16);
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .left .breif .top .num {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: 2px;
  font-feature-settings: "ss08" on, "clig" off, "liga" off;
  font-family: "IBM Plex Sans";
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.16px;
  color: var(--text-tokens-text-secondary);
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .left .breif .top .accountsSelect {
  height: var(--spacing-18);
  display: flex;
  flex-direction: row;
  justify-content: center;
  align-items: center;
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .left .breif .top .accountsSelect .selectInput {
  border: 0;
  height: 40px;
  background: transparent;
  width: 134px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  padding: 0;
  font-feature-settings: "ss08" on, "clig" off, "liga" off;
  font-family: "IBM Plex Sans";
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.16px;
  color: var(--text-tokens-text-primary);
  gap: 0;
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .left .breif .top .accountsSelect .selectOptionswrapper {
  width: 172px;
  top: 22px;
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .left .breif .top .accountsSelect .selectOptionswrapper .PDropdown .dropdownOptions .dropdownOption .dropdownOptionCon {
  padding-left: var(--spacing-8);
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .left .breif .top .accountsSelect .selectOptionswrapper .PDropdown .dropdownOptions .dropdownOption .dropdownOptionCon .left {
  font-feature-settings: "ss08" on, "clig" off, "liga" off;
  font-family: "IBM Plex Sans";
  font-size: 14px;
  font-style: normal;
  font-weight: 400;
  line-height: 18px;
  letter-spacing: 0.16px;
  color: var(--text-tokens-text-primary);
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .left .breif .top .accountsSelect .selectOptionswrapper .PDropdown .dropdownOptions .dropdownOption .dropdownOptionCon .left .placeHolder {
  width: var(--spacing-16);
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .left .breif .top .accountsSelect .selectOptionswrapper .PDropdown .dropdownOptions .dropdownOption .dropdownOptionCon .left > .iconfont {
  font-size: var(--spacing-16);
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .left .breif .bottom {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: var(--spacing-4);
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .left .breif .bottom .balance {
  font-size: 14px;
  font-family: IBM Plex Mono;
  font-weight: 500;
  letter-spacing: 0px;
  line-height: 18px;
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .left .breif .bottom .percent {
  font-size: 14px;
  font-family: IBM Plex Mono;
  font-weight: 400;
  letter-spacing: 0px;
  line-height: 18px;
  color: var(--text-tokens-text-secondary);
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .right {
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  justify-content: flex-end;
  flex: 1;
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .right .tokensWrapper {
  width: 228px;
  display: flex;
  flex-direction: row;
  justify-content: space-between;
  align-items: center;
  flex-direction: row-reverse;
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .right .tokensWrapper .tokens {
  display: flex;
  flex-direction: column;
  justify-content: flex-start;
  align-items: flex-start;
  gap: var(--spacing-4);
  font-feature-settings: "ss08" on, "clig" off, "liga" off;
  font-family: "IBM Plex Sans";
  font-size: 10px;
  font-style: normal;
  font-weight: 400;
  letter-spacing: 0px;
  line-height: 16px;
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .right .tokensWrapper .tokens .splicedIcons {
  gap: var(--spacing-4);
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .right .tokensWrapper .tokens .splicedIcons > img {
  width: var(--spacing-24);
  border-radius: 50%;
  position: static !important;
}
.tableSection .dataSourceItems .dataSourceItem .mainInfo .right .tokensWrapper .tokens .splicedIcons .hideCircle {
  width: var(--spacing-24);
  height: var(--spacing-24);
  position: static !important;
  font-size: 10px;
  font-family: IBM Plex Mono;
  font-weight: 400;
  letter-spacing: 0px;
  line-height: 16px;
}
.tableSection .dataSourceItems .dataSourceItem .extraInfo {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: var(--spacing-16);
}
.tableSection .dataSourceItems .dataSourceItem .extraInfo .card {
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  width: calc(100% - var(--spacing-16) / 2);
  padding: var(--spacing-16);
  gap: var(--spacing-8);
  border-radius: var(--spacing-8);
  border: 1px solid var(--border-tokens-border-primary-selected-gray);
}
.tableSection .dataSourceItems .dataSourceItem .extraInfo .card.active {
  background: var(--layer-tokens-layer-on-primary-selected);
  border-color: var(--border-tokens-border-primary-selected-gray);
}
.tableSection .dataSourceItems .dataSourceItem .extraInfo .card.disabled {
  cursor: not-allowed;
}
.tableSection .dataSourceItems .dataSourceItem .extraInfo .card.availableForAttestation {
  width: 304px;
}
.tableSection .dataSourceItems .dataSourceItem .extraInfo .card .iconfont {
  font-size: var(--spacing-24);
}
.tableSection .dataSourceItems .dataSourceItem .extraInfo .card .txtWrapper {
  font-feature-settings: "ss08" on, "clig" off, "liga" off;
  font-family: "IBM Plex Sans";
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  letter-spacing: 0px;
  line-height: 18px;
}
.tableSection .dataSourceItems .dataSourceItem .extraInfo .card .txtWrapper .label {
  width: 100%;
  display: flex;
  flex-direction: row;
  justify-content: flex-start;
  align-items: center;
  gap: var(--spacing-4);
}
.tableSection .dataSourceItems .dataSourceItem .extraInfo .card .txtWrapper .label > span {
  font-feature-settings: "ss08" on, "clig" off, "liga" off;
  font-family: "IBM Plex Sans";
  font-size: 14px;
  font-style: normal;
  font-weight: 500;
  letter-spacing: 0px;
  line-height: 18px;
  white-space: nowrap;
}
.tableSection .dataSourceItems .dataSourceItem .extraInfo .card .txtWrapper .label .PTooltip .icon-iconInfo {
  font-size: var(--spacing-12);
}
.tableSection .dataSourceItems .dataSourceItem .extraInfo .card .txtWrapper .label .PTooltip .tooltip {
  top: 22px;
  left: -8px;
  transform: none;
}
.tableSection .dataSourceItems .dataSourceItem .extraInfo .card .txtWrapper .label .PTooltip .tooltip:after {
  left: 10px;
  transform: rotate(45deg);
}
.tableSection .dataSourceItems .dataSourceItem .extraInfo .card .txtWrapper .value {
  font-size: 14px;
  font-family: IBM Plex Mono;
  font-weight: 400;
  letter-spacing: 0px;
  line-height: 18px;
}
.tableSection button.PButton.text.moreBtn {
  height: var(--spacing-16);
  align-self: center;
}
.tableSection button.PButton.text.moreBtn .rotate {
  transform: rotate(180deg);
}