@import '@/assets/newCss/flex.scss';
@import '@/assets/newCss/font.scss';
@import '@/assets/newCss/mixins.scss';

.pageEventDetail {
  @include fcss;
  margin: 0 auto;
  // overflow-y: auto;
  .pageContent {
    padding: 40px 0 20px 0;
    @include fcss;
    gap: var(--spacing-40);
    .eventBrief{
      margin-top: calc(-1 * var(--spacing-24));
    }
    .taskList {
      width: 100%;
      @include fcss;
      gap: var(--spacing-16);
      .title {
        @include HeadlineH2;
      }
      .tasks {
        @include fcss;
        width: 100%;
        gap: var(--spacing-16);
        .task {
          width: 100%;
          @include clickableCard;
          @include frbc;
          padding: 0 var(--spacing-40) 0 var(--spacing-24);
          height: 72px;
          .left {
            @include frsc;
            gap: var(--spacing-16);
            @include HeadlineH3;
          }
          .right {
            @include frsc;
            gap: var(--spacing-40);
            .process {
              @include fcss;
              align-items: flex-end;
              gap: var(--spacing-4);
              .txt {
                @include Label01;
                color: var(--text-tokens-text-secondary);
              }
              .bar {
                width: 148px;
                height: var(--spacing-4);
                background: var(--neutral-color-gray-10);
              }
            }
            .PButton {
              height: var(--spacing-32);
            }
          }
        }
      }
    }
  }
}
